<template>
    <div class="app-container sign">
        <el-tabs v-model="activeName" @tab-click="handleClick">
            <el-tab-pane label="" name="first">
                <el-form :inline="true" class="search-member-form">
                    <el-form-item label="">
                        <el-input v-model="mobile" placeholder="" icon="search" :on-icon-click="handleIconClick"></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-tooltip effect="dark" :content="(isCanReadCard ? '' : '')" placement="top">
                            <el-input v-model="card_id" placeholder="">
                                <el-button v-if="isCanReadCard" slot="prepend" @click="readCard">
                                    <span class="read-card">
                                        <icon-svg icon-class="readcard" />
                                    </span>
                                </el-button>
                                <el-button v-if="isCanReadCard" slot="append" icon="delete" @click="clearCardID">
                                </el-button>
                            </el-input>
                        </el-tooltip>
                    </el-form-item>
                </el-form>
                <div class="sign-member-info" v-show="isFinded">
                    <el-card class="box-card">
                        <div slot="header" class="clearfix">
                            <span style="line-height: 36px;"></span>
                        </div>
                        <div class="text item">
                            <el-row :gutter="10">
                                <el-col :sm="16">
                                    <el-row :gutter="10">
                                        <el-col class="info-item" :sm="12">
                                            <label>: </label>
                                            <span>{{ memberInfo.name || '' }}</span>
                                        </el-col>
                                        <el-col class="info-item" :sm="12">
                                            <label>: </label>
                                            <span>{{ !memberInfo.sex ? '' : memberInfo.sex === 1 ? '' : ''}}</span>
                                        </el-col>
                                        <el-col class="info-item" :sm="12">
                                            <label>: </label>
                                            <span>{{ memberInfo.mobile || '' }}</span>
                                        </el-col>
                                        <el-col class="info-item" :sm="12">
                                            <label>: </label>
                                            <span>{{ memberInfo.sale_staff || '' }}</span>
                                        </el-col>
                                    </el-row>
                                </el-col>
                                <el-col :sm="8">
                                    <div class="grid-content user-photo"><img v-if="memberInfo.photo" :src="memberInfo.photo" />
                                        <span v-else></span>
                                    </div>
                                </el-col>
                            </el-row>
                        </div>
                    </el-card>
                    <el-card class="box-card">
                        <div slot="header" class="clearfix">
                            <span style="line-height: 36px;"></span>
                        </div>
                        <div class="text item">
                            <el-form class="sign-form" ref="form" :model="form" :rules="formRules" label-width="150px" :label-position="labelPosition">
                                <el-form-item label="" prop="store_id">
                                    <el-radio-group v-model="form.store_id">
                                        <el-radio :label="2"></el-radio>
                                        <el-radio :label="1"></el-radio>
                                    </el-radio-group>
                                </el-form-item>
                                <el-form-item label="" prop="mobile">
                                    <el-radio-group class="card_type" v-model="form.mobile">
                                        <el-radio :label="memberInfo.mobile">
                                            <div>{{ memberInfo.card_id || '' }}</div>
                                            <div>{{ memberInfo.card_type_name || '' }}</div>
                                            <div>{{ memberInfo.card_start_time ? formatDateTime(memberInfo.card_start_time) + '  ' + formatDateTime(memberInfo.card_end_time) : '' }}</div>
                                            <div>
                                                <span style="color: #fd6440">{{ cardEndTime(memberInfo.card_end_time) >= 0 ? cardEndTime(memberInfo.card_end_time) : 0 }} </span>
                                            </div>
                                        </el-radio>
                                    </el-radio-group>
                                </el-form-item>
                                <el-form-item label="">
                                    <el-button type="primary" @click="submitForm('form')"></el-button>
                                </el-form-item>
                            </el-form>
                        </div>
                    </el-card>

                </div>
            </el-tab-pane>
            <el-tab-pane label="" class="sign-tongji" name="second">
                <div class="select_store">
                    <el-select v-model="store_type" placeholder="">
                        <el-option v-for="item in stores" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select>
                </div>
                <el-row :gutter="20">
                    <el-col :sm="8">
                        <el-card class="today-tongji-wrap">
                            <div class="today-tongji">
                                <div class="today-tongji-title">
                                    
                                </div>
                                <div class="today-tongji-count">
                                    {{ todayTongjiCount.today }}
                                </div>
                                <div class="other">
                                    <div>{{ todayTongjiCount.lastDay }} </div>
                                    <div>{{ todayTongjiCount.thisWeek }} </div>
                                    <div>{{ todayTongjiCount.thisMonth }}</div>
                                </div>
                            </div>
                        </el-card>
                    </el-col>
                    <el-col :sm="16" class="my-chart-wrap">
                        <el-card>
                            <div class="count-range">
                                <el-radio-group v-model="countRange">
                                    <el-radio-button label="30"></el-radio-button>
                                    <el-radio-button label="15"></el-radio-button>
                                    <el-radio-button label="7"></el-radio-button>
                                </el-radio-group>
                                <el-date-picker v-model="countRangeByPicker" type="daterange" placeholder="">
                                </el-date-picker>
                            </div>
                            <div class="myChart">
                                <div id="myChart" :style="{width: echat.width + 'px', height: echat.height + 'px'}"></div>
                            </div>
                        </el-card>
                    </el-col>
                </el-row>
            </el-tab-pane>
        </el-tabs>
    </div>
</template>
<script>
import { memberInfo } from '@/api/member';
import { mapState, mapGetters, mapActions } from 'vuex';
import { formatDateTime } from '@/utils';
import { add, getRangeTongjiList } from '@/api/sign';

// 
let echarts = require('echarts/lib/echarts');
// 
require('echarts/lib/chart/line')
// title
require('echarts/lib/component/tooltip')
require('echarts/lib/component/grid')

const elementResizeEvent = require('element-resize-event');

export default {
    data() {
        // 
        const validateEmpty = (rule, value, callback) => {
            if (typeof value !== 'object' ? !value : !value.length) {
                return callback(new Error(rule.errMsg));
            }
            callback();
        }
        return {
            activeName: 'first',
            mobile: '',
            card_id: '',
            memberInfo: {},
            isFinded: false,
            form: {
                store_id: '',
                mobile: ''
            },
            formRules: {
                store_id: [
                    { validator: validateEmpty, errMsg: '', trigger: 'change' }
                ],
                mobile: [
                    { validator: validateEmpty, errMsg: '', trigger: 'change' }
                ]
            },
            store_type: '',
            stores: [
                {
                    value: '0',
                    label: ''
                },
                {
                    value: '1',
                    label: ''
                },
                {
                    value: '2',
                    label: ''
                }
            ],
            todayTongjiCount: {
                today: 0,
                lastDay: 0,
                thisWeek: 0,
                thisMonth: 0
            },
            myChart: null,
            echat: {
                width: '',
                height: ''
            },
            echatData: {
                keys: ["08-16", "08-17", "08-18", "08-19", "08-20", "08-21", "08-22", "08-23", "08-24", "08-25", "08-26", "08-27", "08-28", "08-29", "08-30", "08-31", "09-01", "09-02", "09-03", "09-04", "09-05", "09-06", "09-07", "09-08", "09-09", "09-10", "09-11", "09-12", "09-13", "09-14"],
                values: [0, 0, 4, 1, 1, 1, 1, 1, 0, 0, 3, 7, 11, 4, 2, 4, 0, 2, 1, 0, 3, 3, 0, 1, 1, 0, 4, 0, 1, 0]
            },
            // echat resize
            timerId: null,
            countRange: '30',
            countRangeByPicker: ''
        };
    },
    computed: {
        ...mapGetters([
            'isCanReadCard',
            'cardID'
        ]),
        ...mapState([
            'winsize'
        ]),
        labelPosition() {
            return (this.winsize === 'xs') ? 'top' : 'right';
        },
        signCountReq() {
            console.log('-aaa-', this.countRange);
            switch (this.countRange) {
                case '30':
                    return this.setSignCountReq(30);
                    break;
                case '15':
                    return this.setSignCountReq(15);
                    break;
                case '7':
                    return this.setSignCountReq(7);
                    break;
                default:
                    if (this.countRangeByPicker[0]) {
                        return {
                            store_id: this.store_type,
                            start_time: Math.round(this.countRangeByPicker[0].getTime() / 1000),
                            end_time: Math.round(this.countRangeByPicker[1].getTime() / 1000)
                        }
                    } else {
                        return this.setSignCountReq(30);
                    }
                    break;
            }
        },
        echatOption() {
            return {
                tooltip: {
                    trigger: 'axis'
                },
                grid: {
                    top: '20',
                    right: '20',
                    bottom: '20',
                    left: '20',
                    containLabel: true
                },
                xAxis: {
                    data: this.echatData.keys
                },
                yAxis: {},
                series: [{
                    name: '',
                    type: 'line',
                    smooth: true,
                    data: this.echatData.values
                }]
            };
        }
    },
    methods: {
        ...mapActions([
            'actionReadCard'
        ]),
        clearCardID() {
            this.card_id = '';
        },
        readCard() {
            if (this.isCanReadCard) {
                this.actionReadCard();
                this.card_id = this.cardID;
                console.log('', this.card_id, this.cardID);
            }
        },
        handleClick(tab, event) {
            console.log(tab, event);
            if (tab.label === '') {
                this.myChart.resize();
            }
        },
        handleIconClick(ev) {
            memberInfo({ mobile: this.mobile }).then(response => {
                this.memberInfo = response.data;
                this.isFinded = true;
            })
        },
        submitForm(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.onSubmit();
                } else {
                    this.$message.error('');
                    return false;
                }
            });
        },
        onSubmit() {
            const params = {
                store_id: this.form.store_id,
                card_id: this.form.card_id,
                mobile: this.memberInfo.mobile
            }
            add(params).then(response => {
                this.$message.success('');
            })
        },
        formatDateTime(time) {
            return formatDateTime(time)
        },
        cardEndTime(time) {
            const now = Math.round(new Date().getTime() / 1000);
            return time ? Math.round((time - now) / (60 * 60 * 24)) : '';
        },
        // 
        setTodayTongjiCount() {
            const now = new Date(),
                year = now.getFullYear(),
                month = now.getMonth(),
                day = now.getDate(),
                last = new Date(year, month - 1, day),
                params = {
                    store_id: this.store_type,
                    start_time: Math.round(last.getTime() / 1000),
                    end_time: Math.round(now.getTime() / 1000)
                };
            let today, lastDay, thisWeek = 0, thisMonth = 0;
            getRangeTongjiList(params).then(response => {
                const data = Object.entries(response.data);
                let i = 0;
                for (let [key, value] of data) {
                    if (i > data.length - 8) {
                        console.log('');
                        thisWeek += 1 * value;
                    }
                    thisMonth += 1 * value;
                    if (i === data.length - 2) {
                        lastDay = value;
                    }
                    if (i === data.length - 1) {
                        today = value;
                    }
                    i++;
                }

                console.log(data, {
                    today,
                    lastDay,
                    thisWeek,
                    thisMonth
                });

                this.todayTongjiCount = {
                    today,
                    lastDay,
                    thisWeek,
                    thisMonth
                }
            })

        },
        // 
        getSignCount() {
            getRangeTongjiList(this.signCountReq).then(response => {
                let i = 0;
                this.echatData.keys = [];
                this.echatData.values = [];
                for (let [key, value] of Object.entries(response.data)) {
                    this.echatData.keys[i] = key;
                    this.echatData.values[i] = value;
                    i++;
                }
                this.drawLine();
            })
        },
        drawLine() {
            // 
            this.myChart.setOption(this.echatOption, true);
        },
        setEchatWH() {
            const $myChartElm = document.querySelector('.myChart');
            if (!$myChartElm) {
                return;
            }
            const $myChart = window.getComputedStyle($myChartElm, null);
            const $myChartWrap = window.getComputedStyle(document.querySelector('.my-chart-wrap'), null);
            // 
            const isBaifenbi = /%/.test($myChartWrap.width);
            const width = isBaifenbi ? document.querySelector('.el-tabs').offsetWidth * ($myChartWrap.width.replace('%', '') / 100) - 2 * $myChartWrap.paddingLeft.replace('px', '') : $myChartWrap.width.replace('px', '') - 2 * $myChartWrap.paddingLeft.replace('px', '');
            this.echat.width = width;
            this.echat.height = 1 * $myChart.height.replace('px', '');
            console.log(this.echat, isBaifenbi, width);
        },
        setSignCountReq(days) {
            const now = new Date();
            return {
                store_id: this.store_type,
                start_time: Math.round((now.getTime() - 1000 * 60 * 60 * 24 * days) / 1000),
                end_time: Math.round(now.getTime() / 1000)
            }
        }
    },
    watch: {
        signCountReq() {
            console.log('==', this.signCountReq);
            this.getSignCount();
        },
        card_id() {
            if (!this.card_id) {
                console.log('');
                return;
            }
            // this.card_id = '0002833000';
            memberInfo({ card_id: this.card_id }).then(response => {
                this.memberInfo = response.data;
                this.isFinded = true;
            })
        },
        store_type() {
            this.signCountReq.store_id = this.store_type;
            this.setTodayTongjiCount();
            this.getSignCount();
        },
        countRangeByPicker() {
            // element-ui@1.4.8 pickermodulecountRangepicker
            this.countRange = this.countRangeByPicker;
        }
    },
    created() {
        this.setTodayTongjiCount();
    },
    mounted() {
        this.setEchatWH();
        // domecharts
        this.myChart = echarts.init(document.getElementById('myChart'))
        this.getSignCount();
        const element = document.querySelector('.el-tabs');
        if (!element) {
            return;
        }
        const _this = this;
        elementResizeEvent(element, function() {
            console.log("resized!");
            console.log(element.offsetWidth);
            _this.setEchatWH();

            // 
            // 
            clearTimeout(_this.timerId);
            _this.timerId = setTimeout(function() {
                _this.myChart.resize();
            }, 280);
        });
    }
};
</script>

<style rel="stylesheet/scss" lang="scss">
.sign {
    .search-member-form {
        text-align: center
    }

    .search-member-form>div {
        text-align: left
    }

    .read-card .svg-icon {
        font-size: 20px;
    }

    .info-item {
        font-size: 14px;
        padding: 10px 0;
        line-height: 20px;
    }

    .info-item label {
        color: #97a8be;
        font-size: 14px;
    }

    .user-photo {
        text-align: center;
        font-size: 14px;
    }

    .user-photo img {
        max-width: 100%;
        max-height: 300px;
        box-shadow: 0 0 1px 1px rgba(0, 0, 0, .1);
    }

    .card_type {
        width: 100%;
    }

    .card_type .el-radio {
        display: flex;
        padding: 11px 0;
    }

    .card_type .el-radio+.el-radio {
        margin-left: 0;
    }

    .card_type .el-radio__label {
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;
        width: 80%;
    }

    .card_type .el-radio__label>div {
        padding: 0 5px 10px 5px;
    }

    .sign-tongji {
        .select_store {
            padding-left: 10px;
            margin-bottom: 10px;
        }

        .today-tongji-wrap {
            display: flex;
            align-items: center;
            justify-content: center;
        }

        .today-tongji {
            display: flex;
            flex-direction: column;
            text-align: center;
            color: #3c3c3c;
            .today-tongji-title {
                font-size: 20px;
            }
            .today-tongji-count {
                font-size: 60px;
                line-height: 80px;
            }
            .other {
                color: #a8a8a8;
                font-size: 14px;
                line-height: 20px;
            }
        }

        .el-card {
            margin: 10px 0;
            height: 300px;
        }

        .my-chart-wrap .el-card__body {
            padding: 0;
        }

        .count-range {
            display: flex;
            align-items: center;
            padding: 0 15px;
            height: 70px;
            .el-date-editor {
                margin: 10px;
            }
        }

        .myChart {
            height: 230px;
        }
    }
}
</style>



