import fetch from '@/utils/fetch1';
const qs = require('qs');

export function add(params) {
  return fetch({
    url: '/api/sign/add',
    method: 'post',
    data: qs.stringify(params),
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded'
    }
  });
}

export function getRangeTongjiList(params) {
  return fetch({
    url: '/api/sign/getRangeTongjiList',
    method: 'POST',
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded'
    },
    data: qs.stringify(params)
  });
}

export function getSingleTongji(params) {
  return fetch({
    url: '/api/sign/getSingleTongji',
    method: 'post',
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded'
    },
    data: qs.stringify(params)
  });
}


